.MeetingTimer {
  background-color: var(--petrol-5);
  padding: 5px 10px 7px;
  backdrop-filter: blur(15px);
  border-radius: 16px;
  display: flex;
  align-items: center;

  &-ellipse {
    width: 6px;
    height: 6px;
    background: var(--aquamarin-80);
    margin: 0px 4px;
    border-radius: 50%;
  }

  span {
    color: var(--nightblue-100);
    font-weight: 500;
    font-size: 15px;
    line-height: 22px;
    width: 42px;
  }
}
