.index.wrapper {
  background-size: cover;
  min-height: 100%;
  height: auto;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-flow: column;
}

.index .ag-footer {
  display: flex;
  padding: 0 30px;
  justify-content: space-between;
  align-items: center;
}

.index .ag-main {
  flex-grow: 1;
}

#ag-canvas {
  height: 100%;
}

#ag-canvas video {
  transform: rotateY(0deg) !important;
}

#ag-resize-container {
  background-image: none !important;
  background-color: black;
  display: flex;
  justify-content: center;
  height: center;
}

#ag-resize-container .ag-item {
  border: none !important;
}

html,
body,
#root,
.full {
  height: 100vh;
  min-height: 100%;
  font-size: 14px;
}

img {
  vertical-align: middle;
  max-width: 100%;
}

.ag-main {
  width: 100%;
  height: 100%;
  display: flex;
  flex-flow: column;
  justify-content: center;
  align-items: center;
}

.ag-rounded {
  border-radius: 19px !important;
}

.ag-href {
  color: #fff;
  cursor: pointer;
}

.ag-href:hover {
  color: #fff;
}

.meeting.wrapper {
  height: 100%;
  padding: 0 20px;
}

video {
  max-width: unset;
}

.meeting .ag-main {
  position: relative;
}

.AgoraVideoCall {
  padding: 60px 0;

  &-info {
    width: 100%;
    display: flex;
    justify-content: space-between;
    align-items: center;
    height: 64px;

    &-nurse {
      display: flex;
      color: white;
      align-items: center;

      &__avatar {
        width: 40px;
        height: 40px;
        object-fit: cover;
        border-radius: 20px;
      }

      &__name {
        margin-left: 12px;

        h3 {
          margin-bottom: 0;
        }
      }
    }
  }

  &-btn-group {
    width: 100%;
    margin-top: 64px;
    background: rgba(255, 255, 255, 0.4);
    backdrop-filter: blur(15px);
    border-radius: 24px;
    z-index: 11;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 15px 8px 16px;

    &__left {
      display: flex;
    }
  }

  &-messages {
    height: 100%;

    &__header {
      height: 64px;
      color: white;
      background-color: var(--petrol-80);
      border-radius: 24px 24px 0px 0px;
      display: flex;
      align-items: center;
      padding-left: 20px;
      margin-bottom: 0;
    }

    &__body {
      background-color: white;
      border-radius: 0px 0px 24px 24px;
      height: calc(100% - 64px);

      width: 100%;
      display: flex;
      flex-direction: column;
      justify-content: space-between;
      padding: 12px;

      &-wrapper {
        padding: 12px 8px;

        &-scroll {
          max-height: 570px;
          margin-right: -10px;
          padding-right: 10px;
        }
      }
    }
  }

  &-btn-reverse {
    width: 40px;
    height: 40px;
    position: absolute;
    top: -15px;
    right: 11px;
    background: rgba(252, 252, 254, 0.6);
    box-shadow: inset 0px 1px 1px rgba(255, 255, 255, 0.5);
    backdrop-filter: blur(15px);
    border-radius: 24px;
    border: none;
    z-index: 1;
    display: flex;
    align-items: center;
    justify-content: center;

    i {
      font-size: 24px;
    }
  }
}

.ag-item {
  height: 100%;
  width: 100%;

  div {
    border-radius: 24px;
    overflow: hidden;
  }

  &.small {
    div {
      border: 2px solid white;
      filter: drop-shadow(0px 0px 20px rgba(0, 0, 0, 0.25));
      backdrop-filter: blur(10px);
      border-radius: 16px;
    }
  }
}

.ag-container {
  width: 100%;
  height: 50vw;
  max-height: 524px;
  margin: 0 auto;
  background: rgba(255, 255, 255, 0.1);
  border-radius: 24px;
}
