.ControlButton {
  &__btn {
    z-index: 12;
    opacity: 1;
    color: var(--nightblue-100);
    width: 60px;
    height: 60px;
    cursor: pointer;
    font-size: 28px;
    display: inline-flex;
    justify-content: center;
    align-items: center;
    margin: 0 15px;
    background: rgba(252, 252, 254, 0.6);
    opacity: 0.9;
    box-shadow: inset 0px 1px 1px rgba(255, 255, 255, 0.5);
    backdrop-filter: blur(15px);
    border-radius: 24px;

    &.square {
      border-radius: 0 0 24px 24px;
    }

    &.leave {
      background: #f45c5c;
      box-shadow: 0px 0px 20px #f45c5c, inset 0px 1px 1px rgba(252, 252, 254, 0.5);
      color: white;
    }

    &:hover {
      color: #00b6ed;
    }

    &.disabled,
    &.disabled:hover {
      color: white;
      cursor: not-allowed;
    }
  }
}

.hb-icon-meeting-camera-2 {
  &:before {
    content: '\e956';
  }
  &.off:before {
    content: '\e94b';
  }
}

.hb-icon-meeting-mic-2 {
  &:before {
    content: '\e949';
  }
  &.off:before {
    content: '\e94a';
  }
}
