.MessageInput {
  display: flex;
  align-items: center;

  input {
    padding: 17px 20px;
    background: var(--petrol-5);
    opacity: 0.88;
    border-radius: 30px;
    width: 100%;
    margin-right: 16px;
    border: none;
    background: var(--petrol-5);
    color: var(--nightblue-100);
    font-weight: 500;
    font-size: 15px;
    line-height: 22px;
    letter-spacing: 0.01em;

    &:focus {
      outline: none;
    }
  }

  button {
    border: none;
    background-color: transparent;
    margin-right: 8px;
    color: var(--petrol-80);
    font-size: 28px;
  }

  button:hover {
    color: var(--aquamarin-100);
  }
}
